<template>
    <div id="evaluate">
        <div class="menu">
            <button :class="{'e-active':show==0}" @click="change(0)">全部评论</button>
            <button :class="{'e-active':show==1}" @click="change(1)">好评</button>
            <button :class="{'e-active':show==2}" @click="change(2)">差评</button>
            <button :class="{'e-active':show==3}" @click="change(3)">晒单</button>
        </div>
        <div v-show="show==0" class="show" v-for="(item,index) in evaluate.all" :key="index">
            <div class="e-title">
                <span class="name">{{item.buyerName}}</span>
                <span class="time">{{item.createTime}}</span>
            </div>
            <div class="e-img" v-if="item.postImg.length>0">
                <img v-for="(imgUrl,index) in item.postImg" :key="index+10" :src="imgUrl" alt="">
            </div>
            <div class="ev">{{item.postDescribe}}</div>
            <div class="return-msg" v-if="item.adminReviews">{{item.adminReviews}}</div>
        </div>
        <div v-show="show==1" class="show" v-for="(item,index) in evaluate.good" :key="index+20">
            <div class="e-title">
                <span class="name">{{item.buyerName}}</span>
                <span class="time">{{item.createTime}}</span>
            </div>
            <div class="e-img">
                <img v-for="(imgUrl,index) in item.postImg" :key="index+30" :src="imgUrl" alt="">
            </div>
            <div class="ev">{{item.postDescribe}}</div>
            <div class="return-msg" v-if="item.adminReviews">{{item.adminReviews}}</div>
        </div>
        <div v-show="show==2" class="show" v-for="(item,index) in evaluate.bad" :key="index+40">
            <div class="e-title">
                <span class="name">{{item.buyerName}}</span>
                <span class="time">{{item.createTime}}</span>
            </div>
            <div class="e-img">
                <img v-for="(imgUrl,index) in item.postImg" :key="index+50" :src="imgUrl" alt="">
            </div>
            <div class="ev">{{item.postDescribe}}</div>
            <div class="return-msg" v-if="item.adminReviews">{{item.adminReviews}}</div>
        </div>
        <div v-show="show==3" class="show" v-for="(item,index) in evaluate.postForm" :key="index+60">
            <div class="e-title">
                <span class="name">{{item.buyerName}}</span>
                <span class="time">{{item.createTime}}</span>
            </div>
            <div class="e-img">
                <img v-for="(imgUrl,index) in item.postImg" :key="index+70" :src="imgUrl" alt="">
            </div>
            <div class="ev">{{item.postDescribe}}</div>
            <div class="return-msg" v-if="item.adminReviews">{{item.adminReviews}}</div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            show:0,
        }
    },
    props:['evaluate'],
    created(){
        // console.log(this.evaluate)
    },
    methods:{
        change(val){
            this.show = val;
        }
    }
}
</script>
<style scoped>

.menu{
    display: flex;
    justify-content: space-around;
    height:.7rem;
    border-top:1px solid #e5e5e5;
    border-bottom:1px solid #e5e5e5;
    padding-top:.1rem;
}
.menu button{
    width:1.4rem;
    height:.5rem;
    line-height: .5rem;
    font-size:.28rem;
    background-color:#999999;
    color:#fff;
    border-radius: .2rem;
}
.menu .e-active{
    background-color:#ff9900;
}
.show {
    padding: 0.1rem 0.45rem;
    border-bottom:1px solid #dddddd;
}
.e-title{
    display: flex;
    justify-content: space-between;
    margin:.16rem;
}
.name{
    font-size: .24rem;
    color:#ffa013;
}
.time{
    font-size: .16rem;
    color:#acacac;
}
.e-img img{
    height:1.5rem;
}
.ev{
    color:#999999;
    font-size: .18rem;
}
.return-msg{
    margin: .1rem 0;
    padding: .18rem .1rem;
    background-color:#dddddd;
    color:#666666;
    border-radius: .1rem;
}
</style>


