<template>
    <div id="car">
            <button id="back" @click='back'><span class="iconfont icon-backicon1"></span></button>
        <section class="items">
            <ul>
                <li v-for="(goods,key) in carArr" :key="goods.cid">
                    <p class="store"><span class="iconfont icon-store" ></span>{{goods.store}}</p>
                    <div class="goods">
                        <div class="choose" v-show="!editShow">
                            <span :class="{iconfont:true,'icon-quan':!goods.tag,'icon-xuanzhong':goods.tag}" @click="turn(key)"></span></div>
                        <img :src='goods.img'>
                        <div class="content">
                            <!-- <span>{{goods.name}}</span><span class="tip">{{goods.tip}}</span> -->
                            <span>{{goods.name}}</span>
                            <ul class="num">
                                <li @click="add(key)">+</li>
                                <li>{{goods.num}}</li>
                                <li @click="reduce(key)">-</li>
                            </ul>
                        </div>
                        <div class="price">&yen;{{goods.price}}</div>
                        <button class="del" v-show="editShow" @click="del(key)">删除</button>
                    </div>
                </li>
            </ul>
        </section>
        <section class="all">
            <div class="button">
                <div class="fl">
                    <div><span :class="{iconfont:true, 'icon-quan':!allChose,'icon-xuanzhong':allChose}" @click="turnAC"></span><p>全选</p></div>
                    <div><span class="iconfont icon-edit" @click="edit"></span><p>编辑</p></div>
                </div>
                <div class="fr">
                    <p>合计：{{priceAll}}</p>
                    <p>（不包含运费）</p>
                </div>
            </div>
            <button id="settle">去结算</button>
        </section>
    </div>
</template>
<script>
import {mapState,mapMutations} from 'vuex'
export default ({
    data(){
        return {
            // res:[
            //     {id:1,store:'杭州保税区仓',img:'/static/images/car/goods1.png',name:'ChildLife/童年时光 婴幼...',tip:'90粒装',num:'1',price:118,tag:false},
            //     {id:2,store:'杭州保税区仓',img:'/static/images/car/goods1.png',name:'ChildLife/童年时光 婴幼...',tip:'90粒装',num:'1',price:118,tag:false},
            // ],
            editShow:false
        }
    },
    created(){
        // console.log(this.carArr)
        this.$bus.$emit('header','购物车',2)
    },
    methods:{
        ...mapMutations(['change','delCar']),
        turn(key){
            // this.carArr[key].tag = !this.carArr[key].tag;
            let tag = !this.carArr[key].tag;
            
            this.change({cid:this.carArr[key].cid,tag})

        },
        add(key){
            let num = this.carArr[key].num +1
            this.change({cid:this.carArr[key].cid,num})
        },
        reduce(key){
            if(this.carArr[key].num>1){
                let num =  this.carArr[key].num-1;
                this.change({cid:this.carArr[key].cid,num})
            }
        },
        edit(){
            this.editShow = !this.editShow;
        },
        del(key){
            this.delCar(this.carArr[key].cid)
        },
        turnAC(){
            this.allChose = !this.allChose
        },
        back(){
            this.$router.go(-1)
        }
    },
    computed:{
        priceAll(){
            let all = 0;
            this.carArr.forEach(item=>{
                if(item.tag){
                    all += item.price * item.num
                }
            })
            return all.toFixed(2);
        },
        allChose:{
            get(){
                let tag = true;
                this.carArr.forEach(item=>{
                    if(!item.tag){
                        tag = false;
                    }
                })
                return tag;
            },
            set(v){
                this.carArr.forEach((item,key)=>{
                    // this.carArr[key].tag = v;
                    this.change({cid:this.carArr[key].cid,tag:v})
                })
            }
        },
        ...mapState(['carArr'])
    }
})
</script>
<style scoped>
#car{
    display: flex;
    flex-direction: column;
    height: 100%;
}
#back{
    position:absolute;
    top:.2rem;
    left:.2rem;
}
.icon-backicon1{
    background-color: #ff9900;
    color:#fff;
    font-size: 2em;
    border:1px solid #ff9900;
}
/* 商品 */
.del{
    width: 1rem;
    color:#ffeaca;
    background-color: #ff9900;
}
.goods{
    display: flex;
}
.icon-store{
    padding:0 .15rem;
    font-size: 1.2em;
    vertical-align: middle;
}
.iconfont{
    font-size: .4rem;
}
.choose{
    display: flex;
    align-items: center;
    padding:0 0 0 .15rem;
}
.goods img{
    width:1.6rem;
    padding:.15rem;
}
.content{
    flex:1;
    font-size: 1.2em;
}
.items{
    flex:1;
}
.price{
    padding:.4rem;
    display: flex;
    align-items: center;
}
.tip{
    padding-left: .5rem;
    font-style: .8em;
    color:#d7d7d7;
}
.num{
    display: flex;
    margin-top:.2rem;
    border-left:1px solid #d8d8d8
}
.num li{
    padding: 0 .2rem;
    border-right:1px solid #d8d8d8;
    border-top:1px solid #d8d8d8;
    border-bottom:1px solid #d8d8d8;
}
.store{
    padding:.2rem 0;
    font-style: .28rem;
    color:#adadad
}
/* all */
.all{
    display: flex;
    width:100%;
    border-top:1px solid #f1f1f1;
    height:1.2rem;
    background-color: #ffffff;
}
.all .button{
    flex:1
}
.all .button .fl div{
    float:left;
    padding:.2rem .3rem;
}
.all .button .fr{
    padding: .2rem .5rem 0 0;
    text-align: center;
    color:#c2c2c2
}
.all .iconfont{
    font-size: .5rem;
}
#settle{
    line-height:1.2rem;
    padding: 0 .75rem;
    background-color: #ff5500;
    color:#fff;
    font-size: 1.5em;
}
</style>


