<template>
    <div id="login">
        <header class="header">
           <span class="iconfont icon-backicon1" @click="back"></span>
           <p class="mtitle">登录</p>
           <router-link to="/register">注册</router-link>
        </header>
        <section class="login-box">
            <ul>
                <li>
                    <span>手机号：</span>
                    <input type="text" v-model.number="phone">
                </li>
                <li>
                    <span>密码：</span>
                    <input type="password" v-model="psd">
                </li>
            </ul>
            <div class="forget">忘记密码</div>
            <button class="login-btn" @click="login">登录</button>
        </section>
    </div>
</template>
<script>
import { Toast } from 'mint-ui';
export default {
    data(){
        return {
            phone:null,
            psd:''
        }
    },
    methods:{
        back(){
            this.$router.go(-1)
        },
        login(){
            let regPhone = /^1[3456789]\d{9}$/;
            let regPsd = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,12}$/;
            let phoneT = '请输入正确的手机号码';
            let psdT = '请输入包含数字和字母，在6-12位的密码';
            if(!(regPhone.test(this.phone))){
                Toast({message:phoneT,duration: 1200});
            }else if(!regPsd.test(this.psd)){
                Toast({message:psdT,duration: 1200});
            }else{
                this.$axios.get('/api/login',{
                    params:{
                        phone:this.phone,
                        password:this.psd
                    }
                }).then(res=>{
                    localStorage.setItem('user',this.phone)
                    this.$router.go(-1)
                })
            }
        }
    }
}
</script>
<style  scoped>
#login{
    width:100%;
    height: 100%;
    background-color:#fff;
    font: size 0.36rem;
}
.header{
    display:flex;
    justify-content: space-between;
    position: relative;
    height: .9rem;
    line-height: .9rem;
    background-color: #ff9900;
    padding: 0 .3rem;
    text-align: center;
    font-size: .36rem;
    color:#fff8ed;
}
.header span{
    font-size: .4rem;
} 
.login-box{
    font-size:.32rem;
    line-height: .8rem;
    padding: 2rem .6rem
}
.login-box li{
    padding: .2rem 0;
    border-bottom: 1px solid #a4a4a4;
}
.login-box span{
    display: inline-block;
    width:1.3rem;
    text-align:justify;
    text-align-last: justify;
}
.login-box input{
    height: .5rem;
    color:#999;
    font-size: .3rem;
}
.forget{
    font-size:.3rem;
    line-height:1rem;   
    text-align: right;
}
.login-btn{
    width:100%;
    height: 1rem;
    line-height: 1rem;
    color:#fff;
    background-color: #ff9900;
    border-radius: .1rem;
}

</style>


