<template>
    <div class="classify">
        <!-- <header>
            <router-link to="/search"><img src="../../assets/images/index/search.png" alt=""></router-link>
        </header> -->
        <ul class="fl">
            <li v-for="(item,index) in res" :key="index" :class="{choose:index==choose}" @click="change(index)">{{item.name}}</li>
        </ul>
        <div class="show">
            <div class="item" v-for="(item,index) in show" :key="index">
                <div class="item-box" >
                    <img :src="item.thumbnail" alt="">
                    <p>{{item.name}}</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default ({
    data(){
        return {
            res:[],
            choose:0,
            show:[]
        }
    },
    created(){
        this.$bus.$emit('header','分类',1)
        this.$axios.get('/api/getClassify')
        .then(res=>{
            console.log('分类页面数据请求成功')
            this.res = res.data.data;
            this.show = res.data.data[0].list
        })
    },
    methods:{
        change(index){
            this.show = this.res[index].list;
            this.choose = index;
        }
    }
})
</script>
<style scoped>
header{
    text-align: center;
    width: 100%;
    padding:.2rem;
    background-color:#ff9900;
}
header img{
    width:5.2rem;
}
.classify{
    overflow: hidden;
    height: 100%;
}
.fl{
    width:2.4rem;
    height:100%;
    background-color:#fafafa;

}
.fl li{
    width:100%;
    line-height: .92rem;
    text-align: center;
    font-size: .28rem;
}
.show{
    height:100%;
    background-color:#ffffff;
    overflow: hidden;
}
.choose{
    background-color:#ffffff;
    color:#ffc267;
    border-left:.1rem solid #1d84a7;

}
.item{
    float:left;
    width:50%;
    padding:.15rem;
}
.item-box{
    width:100%;
    height: auto;
    position: relative;
    border:2px solid #51a1bb;
    border-radius: 5px;
    padding:.3rem;
}
.item img{
    width:100%
}
.item p{
    position: absolute;
    bottom:0;
    left:0;
    width:100%;
    height: .5rem;
    line-height: .5rem;
    font-size: .24rem;
    background: #1d84a7;
    color:#d3e7ed;
    text-align:center;
}
</style>


