import Vue from 'vue'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import axios from 'axios'
import './assets/iconfont/iconfont.css'
Vue.use(MintUI)
Vue.prototype.$axios = axios
var eventBus = new Vue();
Vue.prototype.$bus = eventBus;

