<template>
    <div id="search-box">
        <mt-search
          autofocus
          v-model="value"
          cancel-text="取消"
          placeholder="搜索"
          :result='result'>
        </mt-search>
    </div>
</template>
<script>
export default ({
    data(){
        return {
            value:'',
            result:['1','2']
        }
    }
})
</script>

<style scoped>
#search-box{
    height:100%;
    display: flex;
    flex-direction: column;
    font-size:.24rem;
}



</style>


