import Vue from 'vue';
import VueRouter from 'vue-router';
// 全局注册
Vue.use(VueRouter);

// 导入组件，给路由规则使用
import Index from './components/Index';
import Home from './components/index/Home';
import Classify from './components/index/Classify';
import Car from './components/index/Car'
import Mine from './components/index/Mine'
import Search from './components/Search'
import Detail from './components/Detail'
import Login from './components/Login'
import Register from './components/Register'
import Test from './components/Test'
// 定义路由规则
var routes = [
    {path:'/',redirect:'/home'},
    {path:'/',component:Index,children:[
        {path:'/home',component:Home},
        {path:'/classify',component:Classify},
        {path:'/car',component:Car},
        // {path:'/mine/:id/',name:'wode',component:Mine},
        {path:'/mine',name:'wode',component:Mine},
    ]},
    {path:'/search',component:Search},
    {path:'/detail/:id',component:Detail},
    {path:'/login',component:Login},
    {path:'/register',component:Register},
    {path:'/test',component:Test},

]
var router = new VueRouter({routes,mode:'history'})
export default router;


