<template>
    <div>
        123
        {{select}}
        <button @click="addSel">添加</button>

        <button @click="toggle">切换</button>
        
    </div>
</template>
<script>
import {mapState,mapMutations,mapActions,mapGetters} from 'vuex'
export default {
    data(){
        return {
            isshow:false
        }
    },
    computed:{
        ...mapState(['select']),
    },
    methods:{
        ...mapMutations(['addSel']),
        toggle(){
            this.isshow=!this.isshow
        }
    }
}
</script>
<style scoped>
.box{
    width: 300px;
    height: 300px;
    background-color: yellow;
}
</style>


