<template>
    <div id="index">
     <header class="header">
       <p class="mtitle">{{mtitle}}</p>
    </header>
    <!-- 主体 -->
    <section id="main">
      <router-view></router-view>
    </section>
    <!-- 尾部 -->
    <footer>
      <router-link :class="{active:choose==0}" to="/home"><span class="iconfont icon-home"></span>首页</router-link> 
      <router-link :class="{active:choose==1}" to="/classify"><span class="iconfont icon-caidan"></span>分类</router-link> 
      <router-link :class="{active:choose==2}" to="/car"><span class="iconfont icon-gouwuche1"></span>购物车</router-link> 
      <router-link :class="{active:choose==3}" to="/mine"><span class="iconfont icon-rentou"></span>我的</router-link> 
    </footer>
    </div>
</template>
<script scoped>
import Home from './index/Home'
import Car from './index/Car'
import Classify from './index/Classify'
import Mine from './index/Mine'
export default ({
    name:'Index',
    data(){
        return {
          choose:0,
          mtitle:'首页'
        }
    },
    components:{
        Home,Car,Classify,Mine
    },
    methods:{
     
    },
    created(){
      let _this = this
      this.$bus.$on('header',function(val,n){
        // console.log('test',_this)
        _this.mtitle = val
        _this.choose = n
      })
    }
})
</script >
<style>
#index{
  height:100%;
  display: flex;
  flex-direction: column;
  font-size:.24rem;
}
.header{
    position: relative;
    height: .9rem;
    background-color: #ff9900;
    padding: 0 .2rem;
}
.mtitle{
    text-align: center;
    font-size: 1.8em;
    line-height: .9rem;
    color:#fff8ed;
}
#main{
  flex:1;
  overflow: scroll;
  height: 100%;
  /* padding:0 .2rem; */
}
footer{
  display: flex;
  align-items: stretch;
  background-color: #fff;
  height:1rem;
  padding:0px .2rem;
  border-top:1px solid #eeeeee;
}
footer a{
  flex:1;
  flex-direction: column;
  justify-content: center;
  display: flex;
  font-size: .24rem;
  align-items: center;
  color:#838383;
}
footer .iconfont{
  font-size:.5rem;
}
 .active{
  color:#ff9900;
}
</style>


