<template>
    <div id="mine_header">
        <div class="port">
            <div class="box">
                <div :class="{'img-box':true,'op':!status}"><img class="tx" v-if="status" src="../../assets/images/tx.png" alt=""><router-link v-if="!status" to="/login"></router-link></div>
                <p v-if="!status"><router-link  to="/login">点击登录</router-link></p> 
                <p v-if="status"></p>
            </div>
        </div>
        <div class="collect">
            <span class="iconfont icon-xin"></span>
            我的收藏<span v-if="status">()</span>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            
        }
    },
    props:['status']
}
</script>
<style scoped>
#mine_header{
    display: flex;
    flex-direction: column;
    margin-top:-1px;
    height: 5rem;
    background-image:linear-gradient(to bottom,#ff9900 50%,#fafafa 50%);
    padding:0 .2rem;
    font-size: .3rem;
}

.port{
    flex:1
}
.collect{
    font-size: .36rem;
    height: .85rem;
}
.collect span{
    color: #ee6e6b;
    font-size: .4rem;
    padding:.2rem;
}
.box{
    /* height: 1rem; */
    /* background-color:red; */
    margin:1.1rem auto 0;
    text-align: center;
}
.img-box{
    width:2.1rem;
    height:2.1rem;
    margin:0 auto;
    background-color:#f0ecec;
    border-radius: 50%;
    border:.1rem solid #fff;
    /* opacity: 0.8; */
    overflow: hidden;
}
.img-box a{
    display: block;
    width:100%;
    height: 100%;
}
.box p{
    padding:.15rem;
}
.tx{
    width:100%;
}
.op{
    opacity: 0.8;
}
</style>


