<template>
    <div class="footer">
        <div class="price-all">总价：<span class="money">&yen; 0.00</span></div>
        <button class="add" @click="buy(0)">加入购物车</button>
        <button class="pay" @click="buy(1)">直接购买</button>
    </div>
</template>
<script>
export default {
    data(){
        return {
        }
    },
    methods:{
        buy(n){
            this.$bus.$emit('option',n)
        }
    }
}
</script>
<style  scoped>
/* footer */
.footer{
    display: flex;
    position:relative;
    height: 1rem;
    border-top:1px solid #e1e1e1;
    background-color: #fff;
    line-height: 1rem;
    font-size: .3rem;
}
.money{
    color:#eb2424;
}
.price-all{
    flex: 1;
    padding-left:.3rem
}
.footer button{
    width:2rem;
    color:#fff;
}
.add{
    background-color:#ff9900;
}
.pay{
    background-color:#eb2222;
}


</style>


