import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex)
const store=new Vuex.Store({
    state:{
        carArr:[],
    },
    mutations:{
        addSel(state,data){
            state.carArr.push(data)
        },
        addNum(state,obj){
            state.carArr[obj.index].num += obj.num
        },
        change(state,data){
            // console.log(data)
            let index =  state.carArr.findIndex(item=>{
                return  item.cid==data.cid
            })
            for(let key in data){
                state.carArr[index][key] = data[key]
            }
        },
        delCar(state,cid){
            let index =  state.carArr.findIndex(item=>{
                return  item.cid==cid
            })
            state.carArr.splice(index,1)
        }
    },
    actions:{
        add(context,data){
            // console.log(data)
            let index =  context.state.carArr.findIndex(item=>{
                return  item.userId == data.userId && item.cid==data.cid
            })
            // console.log(index,'index')
            if(index>=0){
                context.commit('addNum',{index:index,num:data.num})
            }else{
                context.commit('addSel',data)
            }
            
        }
    }
})
export default store;


