<template>
    <div :class="{fs:!status}">
        <div class="top-button">
            <span class="iconfont icon-setting"></span>
            <span class="iconfont icon-shouyetubiao-10"></span>
        </div>
        <Header :status="status"></Header>
        <Section></Section>
    </div>
</template>
<script>
import Header from './MineHeader'
import Section from './MineSection'
export default ({
    data(){
        return {
            id:null,
            name:null,
            status:0
        }
    },
    methods:{},
    created(){
        this.$bus.$emit('header','我的',3)
        // this.id = this.$route.params.id;
        this.id = this.$route.query.id;
        if(localStorage.getItem('user')){
            this.status=1;
        }
    },
    components:{
        Header,Section
    }
})
</script>
<style>
.fs{
    color:#979797
}
.top-button {
    position: absolute;
    top:0;
    height: .9rem;
    left:0;
    right:0;
    display: flex;
    justify-content: space-between;
    padding:.2rem;
}
.top-button span{
    font-size: .45rem;
    padding:.05rem;
    color:#fff;
    /* line-height: .9rem; */
}
</style>


