<template>
    <div>
        <h3 class="title"><span :class="iconf"></span>{{dataObj.name}}</h3>
        <div class="show">
            <div class="item" v-for="item in dataObj.list" :key="item.id">
                <router-link :to="'/detail/'+item.id">
                <div class="box">
                    <img :src="item.thumbnail">
                    <div class="price">
                        <span class="newp">&yen;{{item.reduct_price}}</span><span class="oldp">&yen;{{item.original_price}}</span>
                    </div>
                    <p style="font-size:.28rem; line-height:.32rem; word-break:normal">{{item.name}}</p>
                </div>
                </router-link>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
           test:1
        }
    },
    props:['dataObj'],
    computed:{
       iconf(){
           if(this.dataObj.name){
               let str = 'iconfont '
               switch(this.dataObj.name){
                    case '今日疯抢': 
                        str+="icon-hot"
                        break;
                    case '母婴专区':
                        str+="icon-baby"
                        break;
                    case "美妆护肤":
                        str+="icon-iconnvhai"
                        break;
                    case "家具生活":
                        str +="icon-tubiaozhizuomoban-"
                        break;
                    case "营养食品":
                        str+="icon-lollipop"
                        break;
                    case "全球直邮":
                        str+="icon-airplane_icon"
                        break;
               }
               return str;
           }
       }
   }
}
</script>
<style scoped>
.show{
    display:flex;
    align-items: stretch;
}
.item{
    width:50%;
    padding:.15rem;
}
.item img{
    width:100%;
}
.box{
    border:1px solid #f1f1f1;
    background-color:#fff;
    padding: .3rem;
    height: 100%;
}
.price{
    height: .5rem;    
}

.newp{
    float: left;
    font-size: .28rem;
    margin-left:.1rem;
    color:#e81425;
    line-height: .5rem;
}
.oldp{
    font-size: .22rem;
    float:right;
    margin-right:.1rem;
    color:#a6a6a6;
    text-decoration: line-through;
    line-height: .5rem;
}
h3{
    padding-top:.2rem;
    padding-bottom:.1rem;
    font-size:.3rem;
}
.iconfont {
    font-size: .5rem;
    padding: 0 .2rem;
    vertical-align: -0.04rem;
}
.icon-hot{
    color:#ffaa2a
}
.icon-baby{
    color:#298aab
}
.icon-iconnvhai{
    color:#b0244d
}
.icon-tubiaozhizuomoban-{
    font-size: .6rem;
    color:#67a0a0;
    /* vertical-align:10px; */
}
.icon-lollipop{
    color:#f0773a;
}
</style>


