<template>
    <div class="bg" v-if="optionShow">  
        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
          <div v-show="optionShow" class=" option" >
            <div class="info">
                <img :src="res.swiperImgArr[0]">
                <div class="info-box">
                    <p class="price">&yen;{{parseInt(res.reduct_price).toFixed(2)}}</p>
                    <p class="save">库存123456件</p>
                    <p>请选择商品属性</p>
                </div>
                <div class="close">
                    <span @click="close">x</span>
                </div>
            </div>
            <div class="num">
                    <p>购买数量</p>
                    <div class="num-choose">
                        <button @click="add">+</button>
                        <span>{{num}}</span>
                        <button @click="reduce">-</button>
                    </div>
            </div>
            <div class="select" v-for="(item,index) in res.buySelect" :key="index">
                <p>{{item.name}}</p>
                <button v-for="(btn,ind) in item.list" :key="ind+10" :class="{'active':classArr[index]==ind}" @click="change(index,ind)">{{btn}}</button>
                                                                                                                        
            </div>
          </div>
        </transition> 
    </div>
</template>
<script>
import { Toast } from 'mint-ui';
import {mapState,mapActions} from 'vuex';
export default {
    data(){
        return {
            optionShow:false,
            classArr:[],
            num:1,
            
        }
    },
    // [0,0]
    computed:{
        ...mapState(['carArr'])
    },
    props:['res'],
    created(){
        this.$bus.$on('option',res=>{
            // console.log(this.res)
            if(this.optionShow && res==0){
                let o={
                    userId:'001',
                    cid:this.res.cid,
                    name:this.res.name,
                    price:this.res.reduct_price,
                    num:this.num,
                    img:this.res.swiperImgArr[0],
                    store:'杭州保税区仓',
                    tag:false
                }
                this.add(o);
                Toast('加入购物车成功');
                // console.log(this.carArr,'data')
            }
            this.optionShow = !this.optionShow
        })
        this.res.buySelect.forEach((item,index)=>{
            this.classArr[index]=0;
        })
        
    },
    methods:{
        change(val1,val2){
            // this.classArr[parseInt(val1)]=val2
            this.$set(this.classArr,val1,val2)
        },
        add(){
            this.num++
        },
        reduce(){
            if(this.num>1){
                this.num--;
            }
        },
        close(){
            this.optionShow = false;
        },
        ...mapActions(['add']),
    },
    filters:{
        price(val){

        },
        
    }
}
</script>
<style scoped>
.bg{
    position: fixed;
    top:0;
    bottom:0;
    left:0;
    right:0;
    width:100%;
    height:100%;
    background-color:rgba(0, 0, 0, 0.8)
}
.option{
    position: absolute;
    padding:0 .4rem .4rem;
    bottom: 1rem;
    width: 100%;
    background-color:#fff;
    height: 6.4rem; 
    /* animation:mymove 1s 1 forwards; */
}
@keyframes mymove
	{
	from {height: 0;}
	to {height: 6.4rem;}
}
.info{
    display: flex;
}
.info img{
    margin-top:-0.3rem;
    width: 2.2rem;
    height: 2.2rem;
    border:.1rem solid #dddddd;
    border-radius: 3px;
}
.info-box{
    margin:.65rem .3rem;
    color:#555555;
    font-size: .28rem;
}
.price{
    color:#eb2222;
}
.close{
    flex:1;
}
.close span{
    float:right;
    margin-top:.2rem;
    font-size: .3rem;
    border:1px solid #999999;
    color:#999999;
    border-radius: 50%;
    padding:3px 8px;
}
.num{
    width:100%;
    display: flex;
    justify-content: space-between;
}
.num-choose{
    display: flex;
}
.num-choose button,.num-choose span{
    line-height: .5rem;
    height: .5rem;
    text-align: center;
}
.num-choose span{
    width:1.2rem;
    border:1px solid #999999;
}
.num-choose button{
    width:.5rem;
    background-color:#999999;
    color:#fff;
}
.select{
    margin:.1rem 0;

}
.select p{
    font-size:.3rem;
    padding:.1rem 0;
}
.select button{
    font-size:.2rem;
    padding:.1rem;
    margin-right: .2rem;
    border-radius: .1rem;
}
.active{
    background-color:#ff9900;
    color:#fff;
}

</style>


