<template>
  <header>
    <div class="btn">
      <span class="fr iconfont icon-gouwuche" @click="jumpCar"></span>
      <span class="fl iconfont icon-backicon1" @click="back"></span>
      <span class="fr iconfont icon-menu" ></span>
    </div>
    <mt-swipe :auto="4000" class="swiper">
      <mt-swipe-item v-for="(item,index) in res.swiperImgArr" :key="index">
        <img :src="item" />
      </mt-swipe-item>
    </mt-swipe>
    <div class="info">
      <div class="title">
        <p>{{res.name}}{{res.name}}</p>
        <div>
          <span class="iconfont icon-xin"></span>
        </div>
      </div>
      <div class="other">
        <div class="price">
          <p class="reduct">&yen;&nbsp;{{res.reduct_price}}</p>
          <p class="original">&yen;&nbsp;{{res.original_price}}</p>
        </div>
        <div class="intro">
          <p class="discount">
            <span>{{res.allowance}}</span>
            <span v-if="res.isFreeShip">包邮</span>
          </p>
          <p class="describe">
            <span v-for="(item,index) in res.describe" :key="index">{{item}}</span>
          </p>
        </div>
      </div>
    </div>
    
  </header>
</template>
<script>
export default {
    props:['res'],
    methods:{
      back(){
        this.$router.go(-1)
      },
      jumpCar(){
        this.$router.push('/car')
      }
    },
    data(){
      return {optionShow:false}
    }
};
</script>
<style  scoped>
/* header */
.header{
  position: relative;
}
.btn{
  position: fixed;
  width:100%;
  height: .8rem;
  z-index: 2;
}
.btn span{
  margin:.1rem .2rem;
  border-radius: 50%;
  width:.55rem;
  height:.55rem;
  text-align: center;
  line-height: .55rem;
  background-color:#b2b2b2;
  color:#fff;
  font-size: .35rem;
}
.swiper {
  width: 100%;
  background-color: #fff;
  height: 5.45rem;
  text-align: center;
}
.swiper img {
  height: 100%;
}
.info {
  background-color: #fff;
  margin-top: 1px;
}
.title {
  display: flex;
  padding: 0.1rem 0 0 0.45rem;
  height: 1rem;
}
.title p {
  flex: 1;
  font-size: 0.28rem;
  line-height: 0.45rem;
  padding-right: 0.5rem;
}
.title div {
  width: 1.2rem;
  height: auto;
  border-left: 2px solid #ededed;
  display: flex;
  align-items: center;
  justify-content: center;
}
.icon-xin {
  font-size: 0.6rem;
}
.other {
  display: flex;
  /* height: 1rem; */
}
.price {
  width: 1.5rem;
  text-align: center;
}
.reduct {
  color: #ec2e2e;
  font-size: 0.35rem;
}
.original {
  color: #afafaf;
  font-size: 0.26rem;
  text-decoration: line-through;
  padding-top: 0.12rem;
}
.intro span {
  display: inline-block;
  margin-right: 0.2rem;
  font-size: 0.18rem;
  padding: 0.05rem 0.1rem;
}
.discount span {
  background-color: #ff9900;
  color: #fff;
}
.describe span {
  border: 1px solid #ff9900;
  color: #ff9900;
}
.describe {
  padding: 0.1rem 0 0.3rem 0;
}
</style>


