                <Show :dataObj="item"></Show>
            </div>
        </section>
    </div>
</template>
<script>
import Show from './HomeShow'
export default ({
    data(){
        return {
            value:'',
            data:[{
                	"name": "今日疯抢",
                	"list": [{
                		"id": "00001",
                		"name": "美国/SUPER MILD沐浴露500ml",
                		"brand": "SUPER MILD",
                		"original_price": "96",
                		"inventory": "1062",
                		"thumbnail": "http://47.106.12.223:8569/static/img/01.png",
                		"product_id": "00021",
                		"time_start": "2018-12-17 06:00:00",
                		"time_end": "2018-12-17 07:00:00",
                		"total": "727",
                		"reduct_price": "88",
