                		"product_id": "00021",
                		"time_start": "2018-12-17 06:00:00",
                		"time_end": "2018-12-17 07:00:00",
                		"total": "727",
                		"reduct_price": "69",
                		"stock": "113"
                	}]
                }]
	}
    },
    methods:{
        fn(){

        }
    },
    created(){
        this.$bus.$emit('header','首页',0)
        this.$axios.get('/api/getIndexData')
        .then(res=>{
            this.data = res.data.data
        })
    },
    components:{
        Show
    }
})
</script>
<style>
header{
    position: relative;
}
.top{
    position:absolute;
    top:0.5rem;
    text-align: center;
    width: 100%;
    padding:.2rem;
}
.top img{
    width:5.2rem;
}
.header-img{
    margin-top:-.5rem;
    z-index: 0;
}
.header-img img{
    width:100%;
}

</style>


