<template>
  <section class="item-detail">
    <ul class="menu">
      <li>
        <span :class="{'menu-active':menuChoose==0}" @click="menuchange(0)">商品详情</span>
      </li>
      <li>
        <span :class="{'menu-active':menuChoose==1}" @click="menuchange(1)">买家口碑</span>
      </li>
    </ul>
    <div class="show-img" v-show="menuChoose==0">
      <img v-for="(item,index) in res.shopDes" :key="index" :src="item" alt class="des" />
    </div>
    <div class="show" v-show="menuChoose==1">
      <Evaluate :evaluate="res.buyerReviews"></Evaluate>
    </div>
  </section>
</template>
<script>
import Evaluate from './Evaluate'
export default {
    props:["res"],
    data(){
        return {
            menuChoose:0,
        }
    },
    components:{Evaluate},
    methods: {
      menuchange(val) {
        this.menuChoose = val;
      }
    },


};
</script>
<style  scoped>

/* item-detail */
.item-detail {
  position:relative;
  margin-top: 0.3rem;
  padding: 0.1rem 0;
  background-color: #fff;
}
.menu {
  display: flex;
  padding: 0.15rem 0;
}
.menu li {
  flex: 1;
  text-align: center;
}
.menu span {
  display: inline-block;
  padding: 0.12rem 0.3rem;
}
.menu-active {
  border-bottom: 2px solid #ff9900;
  color: #ff9900;
}
.show-img {
  display: flex;
  flex-direction: column;
  padding: 0.1rem 0.45rem;
}
.des {
  width: 100%;
}
/* 购买选项 */
.option{
  width:100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 10;
}
</style>


